import React, { Component } from "react";
import {Helmet} from 'react-helmet'

class PageHelmet extends Component{
    render(){
        return(
            <React.Fragment>
                <Helmet>
                    <title>{this.props.pageTitle} | Full Stack Developer Portfolio </title>
                    <meta name="description" content="Sivanesh Shanmugam's Portfolio. A Full Stack developer based on India." />
                </Helmet>
            </React.Fragment>
        )
    }
}


export default PageHelmet;
