import React from 'react';
import {FaTwitter ,FaNpm , FaLinkedinIn, FaDev, FaGithub, FaStackOverflow } from "react-icons/fa";


const SocialShare = [
    {Social: <FaNpm /> , link: 'https://www.npmjs.com/~sivanesh_s'},
    {Social: <FaLinkedinIn /> , link: 'https://www.linkedin.com/in/sivanesh-shanmugam'},
    {Social: <FaTwitter /> , link: 'https://twitter.com/sivanesh_fiz'},
    {Social: <FaGithub />, link: 'https://github.com/Sivanesh-S'},
    {Social: <FaDev/>, link: 'https://dev.to/sivaneshs'},
    {Social: <FaStackOverflow />, link: 'https://stackoverflow.com/users/6940525/sivanesh-s'}

]

const FooterTwo = () => {
    return (
        <div className="footer-style-2 ptb--30 bg_image bg_image--1" data-black-overlay="6">
            <div className="wrapper plr--50 plr_sm--20">
                <div className="row align-items-center justify-content-between">
                    <div className="col-lg-4 col-md-6 col-sm-6 col-12">
                        <div className="inner">
                            <div className="logo text-center text-sm-left mb_sm--20">
                                <a href="/home-one">
                                    <img className="rounded-circle" src="./assets/images/sivanesh1-logo.jpeg" alt="Logo images"/>
                                </a>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-4 col-md-6 col-sm-6 col-12">
                        <div className="inner text-center">
                            <ul className="social-share rn-lg-size d-flex justify-content-center liststyle">
                                {SocialShare.map((val , i) => (
                                    <li key={i}><a href={`${val.link}`} rel="noopener noreferrer">{val.Social}</a></li>
                                ))}
                            </ul>
                        </div>
                    </div>
                    <div className="col-lg-4 col-md-12 col-sm-12 col-12">
                        <div className="inner text-lg-right text-center mt_md--20 mt_sm--20">
                            <div className="text">
                                <p>Contact me @ <br></br><code>sivanesh.code@gmail.com</code></p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default FooterTwo;