import React, { Component } from "react";

const PortfolioListContent = [
    {
        image: 'https://raw.githubusercontent.com/Sivanesh-S/portfolio-minimal/master/README_DOCS/md.JPG',
        category: 'Portfolio WebDev',
        title: 'A Minimalistic Web Portfolio for all devs.',
        link: 'https://dev.to/sivaneshs/a-minimalistic-web-portfolio-for-all-devs-37gh',
        repo: 'https://github.com/Sivanesh-S/portfolio-minimal'
    },
    {
        image: '/assets/images/my-imgs/projects/github-npm-blog.png',
        category: 'OAuth Github npm',
        title: 'OAuth login using GitHub',
        link: 'https://www.npmjs.com/package/github-oauth-express',
        repo: 'https://github.com/Sivanesh-S/github-oauth-express'
    },
    {
        image: '/assets/images/my-imgs/projects/github-npm-blog.png',
        category: 'Gist Client npm',
        title: 'Gist NodeJS Client',
        link: 'https://www.npmjs.com/package/gist-oauth',
        repo: 'https://github.com/Sivanesh-S/gist-oauth'
    },
    {
        image: '/assets/images/my-imgs/projects/github-npm-blog.png',
        category: 'ESLint Plugin React',
        title: ' A rule with plugin for react-redux package which alerts users for performance issues in mapStateToProps.',
        link: 'https://www.npmjs.com/package/@zohodesk/eslint-plugin-react-performance',
    },
    {
        image: '/assets/images/my-imgs/projects/Chatterax-home.JPG',
        category: 'JS Development',
        title: 'Chatterax - A simple incognito chat app with Google OAuth.',
        link: 'https://chatterax.herokuapp.com/',
        repo: 'https://github.com/Sivanesh-S/chat-app'
    },
    {
        image: '/assets/images/my-imgs/projects/Budget.JPG',
        category: 'HTML CSS JS',
        title: 'Budget Manager Online',
        link: 'https://sivanesh-s.github.io/budget-manager/',
        repo: 'https://github.com/Sivanesh-S/budget-manager'
    }
]

class PortfolioList extends Component{
    render(){
        const {column , styevariation } = this.props;
        const list = PortfolioListContent.slice(0 , this.props.item);
        return(
            <React.Fragment> 
                {list.map((value , index) => (
                    <div className={`${column}`} key={index}>
                            <a href={value.link} target="_blank" rel="noopener noreferrer">
                        <div className={`portfolio ${styevariation}`}>
                            <div className="thumbnail-inner">
                                <div className={`thumbnail ${value.image}`} style={{background: `url(${value.image})`}}></div>
                                <div className={`bg-blr-image ${value.image}`}></div>
                            </div>
                            <div className="content">
                                <div className="inner">
                                    <p>{value.category}</p>
                                    <h4><a href="/portfolio-details">{value.title}</a></h4>
                                    <div className="portfolio-button">
                                        {
                                            value.repo && <a className="rn-btn" href={value.repo}  target="_blank" rel="noopener noreferrer">View Repo</a>
                                        }
                                        
                                    </div>
                                </div>
                            </div>
                        </div>
                        </a>
                    </div>
                ))}
               
            </React.Fragment>
        )
    }
}
export default PortfolioList;