import React, { Component } from "react";
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';

// my data
import {aboutMe} from '../../data/description'

class TabsTwo extends Component{
    render(){
        let 
        tab1 = "Main skills",
        tab2 = "Reputations",
        tab3 = "Experience",
        tab4 = "Education & Certification";
        const { tabStyle } = this.props
        return (
          <div>
            {/* Start Tabs Area */}
            <div className="tabs-area">
              <div className="container">
                <div className="row">
                  <div className="col-lg-12">
                    <Tabs>
                      <TabList className={`${tabStyle}`}>
                        <Tab>{tab1}</Tab>
                        <Tab>{tab2}</Tab>
                        <Tab>{tab3}</Tab>
                        <Tab>{tab4}</Tab>
                      </TabList>

                      <TabPanel>
                        <div className="single-tab-content">
                          <ul>
                            {aboutMe.mainSkills.map(skill => {
                              const { description, heading } = skill;
                              const { link, span, text } = heading;
                              return (
                                <li>
                                  <a target="_blank" rel="noopener noreferrer"  href={`${link}`}>
                                    {text} <span>{span}</span>{' '}
                                  </a>
                                  {description}
                                </li>
                              );
                            })}
                          </ul>
                        </div>
                      </TabPanel>

                      <TabPanel>
                        <div className="single-tab-content">
                          <ul>
                            {aboutMe.reputations.map(skill => {
                              const { description, heading } = skill;
                              const { link, span, text } = heading;
                              return (
                                <li>
                                  <a target="_blank" rel="noopener noreferrer" href={`${link}`}>
                                    {text} <span>{span}</span>{' '}
                                  </a>
                                  {description}
                                </li>
                              );
                            })}
                          </ul>
                        </div>
                      </TabPanel>

                      <TabPanel>
                        <div className="single-tab-content">
                          <ul>
                           {aboutMe.experience.map(skill => {
                              const { description, heading } = skill;
                              const { link, span, text } = heading;
                              return (
                                <li>
                                  <a target="_blank" rel="noopener noreferrer" href={`${link}`}>
                                    {text} <span>{span}</span>{' '}
                                  </a>
                                  {description}
                                </li>
                              );
                            })}
                          </ul>
                        </div>
                      </TabPanel>

                      <TabPanel>
                        <div className="single-tab-content">
                          <ul>
                           {aboutMe.education.map(skill => {
                              const { description, heading } = skill;
                              const { link, span, text } = heading;
                              return (
                                <li>
                                  <a target="_blank" rel="noopener noreferrer" href={`${link}`}>
                                    {text} <span>{span}</span>{' '}
                                  </a>
                                  {description}
                                </li>
                              );
                            })}
                          </ul>
                        </div>
                      </TabPanel>
                    </Tabs>
                  </div>
                </div>
              </div>
            </div>
            {/* End Tabs Area */}
          </div>
        );
    }
}



export default TabsTwo;